import React, { useState, useEffect, useCallback } from 'react';
import Card from '../common/Card';
import { subscriptionService } from '../../services/subscriptionService';
import { SubscriptionPlan, UserSubscription, SubscriptionUsage, ExtendedUser } from '../../types/subscription';

interface SubscriptionDashboardProps {
  user: ExtendedUser;
  className?: string;
}

const SubscriptionDashboard: React.FC<SubscriptionDashboardProps> = ({
  user,
  className = ''
}) => {
  const [subscription, setSubscription] = useState<UserSubscription | null>(null);
  const [usage, setUsage] = useState<SubscriptionUsage | null>(null);
  const [plans, setPlans] = useState<SubscriptionPlan[]>([]);
  const [personalAdmins, setPersonalAdmins] = useState<ExtendedUser[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<'overview' | 'plans' | 'admins' | 'billing'>('overview');
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({type, text});
    setTimeout(() => setActionMessage(null), 3000);
  };

  const loadSubscriptionData = useCallback(async () => {
    try {
      setLoading(true);
      
      //   API     
      const isDemoMode = process.env.NODE_ENV === 'production' || window.location.hostname.includes('render.com');
      
      if (isDemoMode) {
        //   
        const demoSubscription: UserSubscription = {
          id: 'demo-sub-1',
          userId: user.id,
          planId: 'professional',
          status: 'active',
          startDate: new Date().toISOString(),
          endDate: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString(),
          autoRenew: true,
          currentUsage: {
            personalAdminsCount: 3,
            totalProjectsCount: 7,
            totalSurveysCount: 45,
            storageUsed: 2.5,
            lastUpdated: new Date().toISOString()
          },
          paymentHistory: []
        };

        const demoUsage: SubscriptionUsage = {
          personalAdminsCount: 3,
          totalProjectsCount: 7,
          totalSurveysCount: 45,
          storageUsed: 2.5,
          lastUpdated: new Date().toISOString()
        };

        const demoPlans = subscriptionService.getDefaultPlans();

        setSubscription(demoSubscription);
        setUsage(demoUsage);
        setPlans(demoPlans);

        //    
        if (user.role === 'super_admin') {
          const demoAdmins: ExtendedUser[] = [
            {
              id: 'admin-1',
              email: 'admin1@demo.com',
              first_name: '',
              last_name: '',
              role: 'admin',
              subscription: undefined,
              parentAdminId: user.id,
              createdBy: user.id,
              isActive: true,
              createdAt: new Date().toISOString(),
              updatedAt: new Date().toISOString()
            },
            {
              id: 'admin-2', 
              email: 'admin2@demo.com',
              first_name: '',
              last_name: '',
              role: 'admin',
              subscription: undefined,
              parentAdminId: user.id,
              createdBy: user.id,
              isActive: true,
              createdAt: new Date().toISOString(),
              updatedAt: new Date().toISOString()
            }
          ];
          setPersonalAdmins(demoAdmins);
        }

        setError(null);
      } else {
        //     API 
        const [subData, usageData, plansData] = await Promise.all([
          subscriptionService.getCurrentSubscription(user.id),
          subscriptionService.getUsage(user.id),
          subscriptionService.getAvailablePlans().catch(() => subscriptionService.getDefaultPlans())
        ]);

        setSubscription(subData);
        setUsage(usageData);
        setPlans(plansData);

        //       
        if (user.role === 'super_admin') {
          const adminsData = await subscriptionService.getPersonalAdmins(user.id);
          setPersonalAdmins(adminsData);
        }
      }
    } catch (err) {
      showActionMessage('error', '     .   .');
      //      
      setError(null);
      const demoPlans = subscriptionService.getDefaultPlans();
      setPlans(demoPlans);
      setSubscription(null);
      setUsage(null);
    } finally {
      setLoading(false);
    }
  }, [user.id, user.role]);

  useEffect(() => {
    loadSubscriptionData();
  }, [loadSubscriptionData]);

  const handleSubscribe = async (planId: string) => {
    try {
      setLoading(true);
      const result = await subscriptionService.subscribeToPlan({
        planId,
        paymentMethod: 'card'
      });
      
      if (result.success) {
        await loadSubscriptionData();
        showActionMessage('success', '  !');
      } else {
        showActionMessage('error', result.error || '    .');
      }
    } catch (err) {
      showActionMessage('error', err instanceof Error ? err.message : '  ');
    } finally {
      setLoading(false);
    }
  };

  const getCurrentPlan = (): SubscriptionPlan | null => {
    if (!subscription) return null;
    return plans.find(p => p.id === subscription.planId) || null;
  };

  const getUsagePercentage = (current: number, limit: number): number => {
    if (limit === -1) return 0; // unlimited
    return Math.min((current / limit) * 100, 100);
  };

  const getUsageColor = (percentage: number): string => {
    if (percentage >= 90) return 'red';
    if (percentage >= 70) return 'yellow';
    return 'green';
  };

  const formatPrice = (price: number, currency: string): string => {
    return new Intl.NumberFormat('ko-KR', {
      style: 'currency',
      currency: currency === 'KRW' ? 'KRW' : 'USD'
    }).format(price);
  };

  if (loading) {
    return (
      <div className={`p-6 ${className}`}>
        <div className="animate-pulse space-y-4">
          <div className="h-6 bg-gray-200 rounded w-1/4"></div>
          <div className="h-32 bg-gray-200 rounded"></div>
          <div className="h-32 bg-gray-200 rounded"></div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className={`p-6 ${className}`}>
        <Card title="">
          <div className="text-red-600">{error}</div>
          <button
            onClick={loadSubscriptionData}
            className="mt-4 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
          >
             
          </button>
        </Card>
      </div>
    );
  }

  const currentPlan = getCurrentPlan();

  return (
    <div className={`space-y-6 ${className}`}>
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-50 px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${actionMessage.type === 'success' ? 'bg-green-100 text-green-800' : actionMessage.type === 'info' ? 'bg-blue-100 text-blue-800' : 'bg-red-100 text-red-800'}`}>
          {actionMessage.text}
        </div>
      )}
      {/*   */}
      <div className="border-b border-gray-200">
        <nav className="-mb-px flex space-x-8">
          {[
            { id: 'overview', name: '', icon: '' },
            { id: 'plans', name: '', icon: '' },
            ...(user.role === 'super_admin' ? [{ id: 'admins', name: ' ', icon: '' }] : []),
            { id: 'billing', name: ' ', icon: '' }
          ].map(tab => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as 'overview' | 'plans' | 'admins' | 'billing')}
              className={`py-2 px-1 border-b-2 font-medium text-sm ${
                activeTab === tab.id
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              <span className="mr-2">{tab.icon}</span>
              {tab.name}
            </button>
          ))}
        </nav>
      </div>

      {/*   */}
      {activeTab === 'overview' && (
        <div className="space-y-6">
          {/*    */}
          <Card title="  ">
            {subscription && currentPlan ? (
              <div className="space-y-4">
                <div className="flex justify-between items-center">
                  <div>
                    <h3 className="text-lg font-medium">{currentPlan.name}</h3>
                    <p className="text-gray-600">{currentPlan.description}</p>
                  </div>
                  <div className="text-right">
                    <div className="text-2xl font-bold">{formatPrice(currentPlan.price, currentPlan.currency)}</div>
                    <div className="text-sm text-gray-500"></div>
                  </div>
                </div>
                
                <div className="grid grid-cols-2 gap-4 p-4 bg-gray-50 rounded">
                  <div>
                    <div className="text-sm text-gray-600"> </div>
                    <div className="font-medium">{new Date(subscription.startDate).toLocaleDateString('ko-KR')}</div>
                  </div>
                  <div>
                    <div className="text-sm text-gray-600"> </div>
                    <div className="font-medium">{new Date(subscription.endDate).toLocaleDateString('ko-KR')}</div>
                  </div>
                </div>

                <div className={`px-4 py-2 rounded ${
                  subscription.status === 'active' ? 'bg-green-100 text-green-800' :
                  subscription.status === 'cancelled' ? 'bg-red-100 text-red-800' :
                  'bg-yellow-100 text-yellow-800'
                }`}>
                  : {subscription.status === 'active' ? '' : 
                        subscription.status === 'cancelled' ? '' : ''}
                </div>
              </div>
            ) : (
              <div className="text-center py-8">
                <div className="text-gray-500 mb-4">   .</div>
                <button
                  onClick={() => setActiveTab('plans')}
                  className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
                >
                   
                </button>
              </div>
            )}
          </Card>

          {/*   */}
          {usage && currentPlan && (
            <Card title=" ">
              <div className="space-y-4">
                {[
                  { label: ' ', current: usage.personalAdminsCount, limit: currentPlan.limits.maxPersonalAdmins },
                  { label: ' ', current: usage.totalProjectsCount, limit: currentPlan.limits.maxPersonalAdmins * currentPlan.limits.maxProjectsPerAdmin },
                  { label: ' ', current: usage.totalSurveysCount, limit: usage.totalProjectsCount * currentPlan.limits.maxSurveysPerProject },
                  { label: '  (GB)', current: usage.storageUsed, limit: currentPlan.limits.storageLimit }
                ].map(item => {
                  const percentage = getUsagePercentage(item.current, item.limit);
                  const color = getUsageColor(percentage);
                  
                  return (
                    <div key={item.label}>
                      <div className="flex justify-between text-sm mb-1">
                        <span>{item.label}</span>
                        <span>
                          {item.current} / {item.limit === -1 ? '' : item.limit}
                        </span>
                      </div>
                      <div className="w-full bg-gray-200 rounded-full h-2">
                        <div
                          className={`h-2 rounded-full bg-${color}-500`}
                          style={{ width: `${percentage}%` }}
                        ></div>
                      </div>
                      {percentage >= 90 && item.limit !== -1 && (
                        <div className="text-red-600 text-xs mt-1">
                            .  .
                        </div>
                      )}
                    </div>
                  );
                })}
              </div>
            </Card>
          )}
        </div>
      )}

      {/*   */}
      {activeTab === 'plans' && (
        <div className="space-y-6">
          <Card title=" ">
            <div className="grid md:grid-cols-3 gap-6">
              {plans.map(plan => (
                <div
                  key={plan.id}
                  className={`border rounded-lg p-6 relative ${
                    plan.isPopular ? 'border-blue-500 bg-blue-50' : 'border-gray-200'
                  } ${currentPlan?.id === plan.id ? 'ring-2 ring-green-500' : ''}`}
                >
                  {plan.isPopular && (
                    <div className="absolute -top-3 left-1/2 transform -translate-x-1/2">
                      <span className="bg-blue-500 text-white px-3 py-1 rounded-full text-sm"></span>
                    </div>
                  )}
                  
                  {currentPlan?.id === plan.id && (
                    <div className="absolute -top-3 right-4">
                      <span className="bg-green-500 text-white px-3 py-1 rounded-full text-sm"> </span>
                    </div>
                  )}

                  <div className="text-center mb-4">
                    <h3 className="text-xl font-bold">{plan.name}</h3>
                    <p className="text-gray-600 text-sm mt-1">{plan.description}</p>
                    <div className="mt-4">
                      <span className="text-3xl font-bold">{formatPrice(plan.price, plan.currency)}</span>
                      <span className="text-gray-500">/</span>
                    </div>
                  </div>

                  <ul className="space-y-2 mb-6">
                    {plan.features.map(feature => (
                      <li key={feature.id} className="flex items-center text-sm">
                        <span className="text-green-500 mr-2"></span>
                        <span>
                          {feature.name}
                          {feature.limit && feature.limit > 0 && (
                            <span className="text-gray-500"> ({feature.limit})</span>
                          )}
                          {feature.limit === -1 && (
                            <span className="text-green-600"> ()</span>
                          )}
                        </span>
                      </li>
                    ))}
                  </ul>

                  <button
                    onClick={() => handleSubscribe(plan.id)}
                    disabled={currentPlan?.id === plan.id || loading}
                    className={`w-full py-2 px-4 rounded font-medium ${
                      currentPlan?.id === plan.id
                        ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                        : 'bg-blue-500 text-white hover:bg-blue-600'
                    }`}
                  >
                    {currentPlan?.id === plan.id ? ' ' : ''}
                  </button>
                </div>
              ))}
            </div>
          </Card>
        </div>
      )}

      {/*    ( ) */}
      {activeTab === 'admins' && user.role === 'super_admin' && (
        <div className="space-y-6">
          <Card title="  ">
            <div className="space-y-4">
              <div className="flex justify-between items-center">
                <h3 className="text-lg font-medium">  </h3>
                <button className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600">
                    
                </button>
              </div>

              {personalAdmins.length > 0 ? (
                <div className="overflow-x-auto">
                  <table className="w-full text-sm">
                    <thead className="bg-gray-50">
                      <tr>
                        <th className="px-4 py-2 text-left"></th>
                        <th className="px-4 py-2 text-left"></th>
                        <th className="px-4 py-2 text-left"> </th>
                        <th className="px-4 py-2 text-left"></th>
                        <th className="px-4 py-2 text-left"></th>
                        <th className="px-4 py-2 text-left"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {personalAdmins.map(admin => (
                        <tr key={admin.id} className="border-t">
                          <td className="px-4 py-2">{admin.first_name} {admin.last_name}</td>
                          <td className="px-4 py-2">{admin.email}</td>
                          <td className="px-4 py-2">0 / {currentPlan?.limits.maxProjectsPerAdmin || 0}</td>
                          <td className="px-4 py-2">{new Date(admin.createdAt).toLocaleDateString('ko-KR')}</td>
                          <td className="px-4 py-2">
                            <span className={`px-2 py-1 rounded text-xs ${
                              admin.isActive ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                            }`}>
                              {admin.isActive ? '' : ''}
                            </span>
                          </td>
                          <td className="px-4 py-2">
                            <button className="text-blue-600 hover:text-blue-800 mr-2"></button>
                            <button className="text-red-600 hover:text-red-800"></button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              ) : (
                <div className="text-center py-8 text-gray-500">
                     .
                </div>
              )}

              {currentPlan && (
                <div className="bg-blue-50 p-4 rounded">
                  <div className="text-sm text-blue-800">
                        {currentPlan.limits.maxPersonalAdmins === -1 ? '' : currentPlan.limits.maxPersonalAdmins} 
                        ,   {currentPlan.limits.maxProjectsPerAdmin === -1 ? '' : currentPlan.limits.maxProjectsPerAdmin} 
                       .
                  </div>
                </div>
              )}
            </div>
          </Card>
        </div>
      )}

      {/*    */}
      {activeTab === 'billing' && (
        <div className="space-y-6">
          <Card title=" ">
            <div className="text-center py-8 text-gray-500">
                 ...
            </div>
          </Card>
        </div>
      )}
    </div>
  );
};

export default SubscriptionDashboard;