import React, { useState, useEffect, useRef } from 'react';
import { CheckCircleIcon, ExclamationTriangleIcon } from '@heroicons/react/24/outline';
import Card from '../common/Card';
import Button from '../common/Button';
import Modal from '../common/Modal';
import { 
  systemManagementService, 
  systemUtils,
  SystemConfiguration,
  BackupStatus,
  SystemHealth,
  MaintenanceTask
} from '../../services/systemManagementService';

interface SystemManagementProps {
  className?: string;
  onError?: (error: string) => void;
  onSuccess?: (message: string) => void;
}

const SystemManagement: React.FC<SystemManagementProps> = ({
  className = '',
  onError,
  onSuccess
}) => {
  const [activeSection, setActiveSection] = useState<'config' | 'backup' | 'maintenance' | 'updates'>('config');
  const [configurations, setConfigurations] = useState<SystemConfiguration[]>([]);
  const [backups, setBackups] = useState<BackupStatus[]>([]);
  const [systemHealth, setSystemHealth] = useState<SystemHealth | null>(null);
  const [maintenanceTasks, setMaintenanceTasks] = useState<MaintenanceTask[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>('');
  const [success, setSuccess] = useState<string>('');
  const [, setRunningTasks] = useState<Set<string>>(new Set());
  const [showConfirmModal, setShowConfirmModal] = useState(false);
  const [confirmAction, setConfirmAction] = useState<{ action: () => Promise<void>; message: string } | null>(null);
  const monitorIntervalsRef = useRef<Set<ReturnType<typeof setInterval>>>(new Set());

  useEffect(() => {
    loadSystemData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // Cleanup all monitor intervals on unmount
  useEffect(() => {
    const intervals = monitorIntervalsRef.current;
    return () => { intervals.forEach(clearInterval); };
  }, []);

  const loadSystemData = async () => {
    setLoading(true);
    setError('');
    
    try {
      // Load configurations
      const configResponse = await systemManagementService.getConfigurations();
      if (configResponse.success && configResponse.data) {
        setConfigurations(configResponse.data);
      } else {
        // Fallback to default configurations
        setConfigurations(getDefaultConfigurations());
      }

      // Load backup status
      const backupResponse = await systemManagementService.getBackups();
      if (backupResponse.success && backupResponse.data) {
        setBackups(backupResponse.data);
      } else {
        setBackups([]);
      }

      // Load system health
      const healthResponse = await systemManagementService.getSystemHealth();
      if (healthResponse.success && healthResponse.data) {
        setSystemHealth(healthResponse.data);
      }

      // Load maintenance tasks
      const tasksResponse = await systemManagementService.getMaintenanceTasks();
      if (tasksResponse.success && tasksResponse.data) {
        setMaintenanceTasks(tasksResponse.data);
      } else {
        // Fallback to default tasks
        setMaintenanceTasks(getDefaultMaintenanceTasks());
      }

    } catch (err: unknown) {
      const errorMessage = (err instanceof Error ? err.message : '') || '     .';
      setError(errorMessage);
      onError?.(errorMessage);
      
      // Use fallback data
      setConfigurations(getDefaultConfigurations());
      setMaintenanceTasks(getDefaultMaintenanceTasks());
    } finally {
      setLoading(false);
    }
  };

  // Default configurations for fallback
  const getDefaultConfigurations = (): SystemConfiguration[] => [
    {
      id: '1',
      category: 'general',
      key: 'system_name',
      value: 'AHP Decision System',
      description: ' ',
      type: 'string'
    },
    {
      id: '2',
      category: 'general',
      key: 'max_users_per_project',
      value: '100',
      description: '   ',
      type: 'number'
    },
    {
      id: '3',
      category: 'security',
      key: 'password_min_length',
      value: '8',
      description: '  ',
      type: 'number'
    },
    {
      id: '4',
      category: 'security',
      key: 'login_attempts_limit',
      value: '5',
      description: '   ',
      type: 'number'
    },
    {
      id: '5',
      category: 'security',
      key: 'session_timeout',
      value: '3600',
      description: '  ()',
      type: 'number'
    },
    {
      id: '6',
      category: 'performance',
      key: 'api_rate_limit',
      value: '1000',
      description: ' API  ',
      type: 'number'
    },
    {
      id: '7',
      category: 'notification',
      key: 'email_notifications',
      value: 'true',
      description: '  ',
      type: 'boolean'
    },
    {
      id: '8',
      category: 'backup',
      key: 'auto_backup_enabled',
      value: 'true',
      description: '  ',
      type: 'boolean'
    },
    {
      id: '9',
      category: 'backup',
      key: 'backup_frequency',
      value: 'daily',
      description: ' ',
      type: 'select',
      options: ['hourly', 'daily', 'weekly', 'monthly']
    }
  ];

  // Default maintenance tasks for fallback
  const getDefaultMaintenanceTasks = (): MaintenanceTask[] => [
    {
      id: 'db_optimize',
      name: ' ',
      category: 'database',
      description: '   ',
      status: 'idle'
    },
    {
      id: 'index_rebuild',
      name: ' ',
      category: 'database', 
      description: '  ',
      status: 'idle'
    },
    {
      id: 'cache_clear',
      name: ' ',
      category: 'cache',
      description: '  ',
      status: 'idle'
    },
    {
      id: 'log_archive',
      name: ' ',
      category: 'logs',
      description: '   ',
      status: 'idle'
    }
  ];

  const handleConfigUpdate = async (configId: string, newValue: string) => {
    const config = configurations.find(c => c.id === configId);
    if (!config) return;
    
    // Validate value
    const validation = systemUtils.validateConfigValue(config, newValue);
    if (!validation.valid) {
      setError(validation.error || ' .');
      return;
    }
    
    setLoading(true);
    setError('');
    
    try {
      const response = await systemManagementService.updateConfiguration(configId, newValue);
      
      if (response.success && response.data) {
        // Update local state
        setConfigurations(prev => prev.map(config => 
          config.id === configId ? { ...config, value: newValue } : config
        ));
        
        const successMessage = ` : ${config.key}`;
        setSuccess(successMessage);
        onSuccess?.(successMessage);
        
        // Clear success message after 3 seconds
        setTimeout(() => setSuccess(''), 3000);
        
        if (response.data.restart_required) {
          setError('      .');
        }
        
      } else {
        throw new Error(response.error || '  .');
      }
      
    } catch (err: unknown) {
      const errorMessage = (err instanceof Error ? err.message : '') || '    .';
      setError(errorMessage);
      onError?.(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  const handleBackupCreate = async (type: 'full' | 'incremental' | 'differential') => {
    const confirmMessage = `${systemUtils.getBackupTypeDisplayName(type)}() ?`;
    
    setConfirmAction({
      action: async () => {
        setLoading(true);
        setError('');
        
        try {
          const response = await systemManagementService.createBackup(type);
          
          if (response.success && response.data) {
            const successMessage = `${systemUtils.getBackupTypeDisplayName(type)}() .`;
            setSuccess(successMessage);
            onSuccess?.(successMessage);
            
            // Monitor backup progress if task_id is provided
            if (response.data?.task_id) {
              const taskId = response.data.task_id;
              setRunningTasks(prev => {
                const newSet = new Set(prev);
                newSet.add(taskId);
                return newSet;
              });
              monitorTask(taskId);
            }
            
            // Refresh backup list
            setTimeout(() => {
              loadSystemData();
            }, 1000);
            
          } else {
            throw new Error(response.error || '  .');
          }
          
        } catch (err: unknown) {
          const errorMessage = (err instanceof Error ? err.message : '') || '    .';
          setError(errorMessage);
          onError?.(errorMessage);
        } finally {
          setLoading(false);
        }
      },
      message: confirmMessage
    });
    
    setShowConfirmModal(true);
  };

  // Monitor background tasks
  const monitorTask = async (taskId: string) => {
    const checkInterval = setInterval(async () => {
      try {
        const response = await systemManagementService.getTaskStatus(taskId);

        if (response.success && response.data) {
          const { status, message } = response.data;

          if (status === 'completed') {
            clearInterval(checkInterval);
            monitorIntervalsRef.current.delete(checkInterval);
            setRunningTasks(prev => {
              const newTasks = new Set(prev);
              newTasks.delete(taskId);
              return newTasks;
            });

            setSuccess(' .');
            loadSystemData(); // Refresh data

          } else if (status === 'failed') {
            clearInterval(checkInterval);
            monitorIntervalsRef.current.delete(checkInterval);
            setRunningTasks(prev => {
              const newTasks = new Set(prev);
              newTasks.delete(taskId);
              return newTasks;
            });

            setError(message || ' .');
          }
        }
      } catch (err) {
        clearInterval(checkInterval);
        monitorIntervalsRef.current.delete(checkInterval);
        setError('     .');
      }

    }, 2000); // Check every 2 seconds

    monitorIntervalsRef.current.add(checkInterval);

    // Stop monitoring after 10 minutes
    setTimeout(() => {
      clearInterval(checkInterval);
      monitorIntervalsRef.current.delete(checkInterval);
      setRunningTasks(prev => {
        const newTasks = new Set(prev);
        newTasks.delete(taskId);
        return newTasks;
      });
    }, 600000);
  };

  // Handle maintenance tasks
  const handleMaintenanceTask = async (taskId: string) => {
    const task = maintenanceTasks.find(t => t.id === taskId);
    if (!task) return;
    
    setConfirmAction({
      action: async () => {
        setLoading(true);
        setError('');
        
        try {
          const response = await systemManagementService.runMaintenanceTask(taskId);
          
          if (response.success && response.data) {
            const successMessage = `  : ${task.name}`;
            setSuccess(successMessage);
            onSuccess?.(successMessage);
            
            // Update task status locally
            setMaintenanceTasks(prev => prev.map(t => 
              t.id === taskId ? { ...t, status: 'running' } : t
            ));
            
            if (response.data?.task_id) {
              const taskId = response.data.task_id;
              setRunningTasks(prev => {
                const newSet = new Set(prev);
                newSet.add(taskId);
                return newSet;
              });
              monitorTask(taskId);
            }
            
          } else {
            throw new Error(response.error || '   .');
          }
          
        } catch (err: unknown) {
          const errorMessage = (err instanceof Error ? err.message : '') || '    .';
          setError(errorMessage);
          onError?.(errorMessage);
        } finally {
          setLoading(false);
        }
      },
      message: `'${task.name}'  ?`
    });
    
    setShowConfirmModal(true);
  };

  // Handle backup actions
  const handleBackupDownload = async (backupId: string) => {
    setLoading(true);
    setError('');
    
    try {
      const response = await systemManagementService.downloadBackup(backupId);
      
      if (response.success) {
        // Handle file download
        setSuccess('  .');
        onSuccess?.('  .');
      } else {
        throw new Error(response.error || '  .');
      }
      
    } catch (err: unknown) {
      const errorMessage = (err instanceof Error ? err.message : '') || '    .';
      setError(errorMessage);
      onError?.(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  const handleBackupRestore = async (backupId: string) => {
    setConfirmAction({
      action: async () => {
        setLoading(true);
        setError('');
        
        try {
          const response = await systemManagementService.restoreBackup(backupId);
          
          if (response.success && response.data) {
            const successMessage = '  .';
            setSuccess(successMessage);
            onSuccess?.(successMessage);
            
            if (response.data?.task_id) {
              const taskId = response.data.task_id;
              setRunningTasks(prev => {
                const newSet = new Set(prev);
                newSet.add(taskId);
                return newSet;
              });
              monitorTask(taskId);
            }
            
          } else {
            throw new Error(response.error || '  .');
          }
          
        } catch (err: unknown) {
          const errorMessage = (err instanceof Error ? err.message : '') || '    .';
          setError(errorMessage);
          onError?.(errorMessage);
        } finally {
          setLoading(false);
        }
      },
      message: '  ?     .'
    });
    
    setShowConfirmModal(true);
  };

  const handleBackupDelete = async (backupId: string) => {
    setConfirmAction({
      action: async () => {
        setLoading(true);
        setError('');
        
        try {
          const response = await systemManagementService.deleteBackup(backupId);
          
          if (response.success) {
            const successMessage = ' .';
            setSuccess(successMessage);
            onSuccess?.(successMessage);
            
            // Remove from local state
            setBackups(prev => prev.filter(b => b.id !== backupId));
            
          } else {
            throw new Error(response.error || '  .');
          }
          
        } catch (err: unknown) {
          const errorMessage = (err instanceof Error ? err.message : '') || '    .';
          setError(errorMessage);
          onError?.(errorMessage);
        } finally {
          setLoading(false);
        }
      },
      message: '   ?'
    });
    
    setShowConfirmModal(true);
  };

  // Handle cache clearing
  const handleCacheClear = async (cacheType: 'application' | 'images' | 'sessions' | 'all') => {
    const typeNames = {
      application: ' ',
      images: ' ',
      sessions: ' ',
      all: ' '
    };
    
    setConfirmAction({
      action: async () => {
        setLoading(true);
        setError('');
        
        try {
          const response = await systemManagementService.clearCache(cacheType);
          
          if (response.success) {
            const successMessage = `${typeNames[cacheType]} .`;
            setSuccess(successMessage);
            onSuccess?.(successMessage);
          } else {
            throw new Error(response.error || '  .');
          }
          
        } catch (err: unknown) {
          const errorMessage = (err instanceof Error ? err.message : '') || '    .';
          setError(errorMessage);
          onError?.(errorMessage);
        } finally {
          setLoading(false);
        }
      },
      message: `${typeNames[cacheType]} ?`
    });
    
    setShowConfirmModal(true);
  };

  // Handle system health check
  const handleHealthCheck = async () => {
    setLoading(true);
    setError('');
    
    try {
      const response = await systemManagementService.runHealthCheck();
      
      if (response.success && response.data) {
        setSystemHealth(response.data);
        setSuccess('   .');
        onSuccess?.('   .');
      } else {
        throw new Error(response.error || '   .');
      }
      
    } catch (err: unknown) {
      const errorMessage = (err instanceof Error ? err.message : '') || '     .';
      setError(errorMessage);
      onError?.(errorMessage);
    } finally {
      setLoading(false);
    }
  };

  const renderConfigurationSection = () => (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h3 className="text-lg font-semibold"> </h3>
        <Button variant="primary"> </Button>
      </div>

      {(['general', 'security', 'performance', 'notification', 'backup'] as const).map(category => {
        const categoryConfigs = configurations.filter(c => c.category === category);
        if (categoryConfigs.length === 0) return null;

        return (
          <Card key={category} title={`${systemUtils.getCategoryDisplayName(category)} `}>
            <div className="space-y-4">
              {categoryConfigs.map(config => (
                <div key={config.id} className="grid grid-cols-1 md:grid-cols-3 gap-4 items-center p-4 border rounded">
                  <div>
                    <div className="font-medium">{config.key}</div>
                    <div className="text-sm text-gray-600">{config.description}</div>
                  </div>
                  <div>
                    {config.type === 'boolean' ? (
                      <select
                        value={config.value}
                        onChange={(e) => handleConfigUpdate(config.id, e.target.value)}
                        className="border rounded px-3 py-2 w-full"
                      >
                        <option value="true"></option>
                        <option value="false"></option>
                      </select>
                    ) : config.type === 'select' ? (
                      <select
                        value={config.value}
                        onChange={(e) => handleConfigUpdate(config.id, e.target.value)}
                        disabled={loading}
                        className="border rounded px-3 py-2 w-full disabled:opacity-50"
                      >
                        {config.options?.map(option => (
                          <option key={option} value={option}>{option}</option>
                        ))}
                      </select>
                    ) : config.type === 'number' ? (
                      <input
                        type="number"
                        value={config.value}
                        onChange={(e) => handleConfigUpdate(config.id, e.target.value)}
                        disabled={loading}
                        className="border rounded px-3 py-2 w-full disabled:opacity-50"
                      />
                    ) : (
                      <input
                        type={config.sensitive ? 'password' : 'text'}
                        value={config.value}
                        onChange={(e) => handleConfigUpdate(config.id, e.target.value)}
                        disabled={loading}
                        className="border rounded px-3 py-2 w-full disabled:opacity-50"
                      />
                    )}
                  </div>
                  <div className="text-sm text-gray-500">
                    : <code className="bg-gray-100 px-1 rounded">{config.value}</code>
                  </div>
                </div>
              ))}
            </div>
          </Card>
        );
      })}
    </div>
  );

  const renderBackupSection = () => (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h3 className="text-lg font-semibold"> </h3>
        <div className="flex space-x-2">
          <Button variant="secondary" onClick={() => handleBackupCreate('incremental')}>
             
          </Button>
          <Button variant="primary" onClick={() => handleBackupCreate('full')}>
             
          </Button>
        </div>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card title="  ">
          <div className="text-center">
            <div className="text-2xl font-bold text-blue-600">
              {backups.length > 0 
                ? backups.reduce((total, backup) => {
                    const sizeInMB = backup.size.includes('GB') 
                      ? parseFloat(backup.size) * 1024 
                      : parseFloat(backup.size);
                    return total + sizeInMB;
                  }, 0).toFixed(1) + ' MB'
                : '0 MB'
              }
            </div>
            <div className="text-sm text-gray-600">  </div>
          </div>
        </Card>
        <Card title=" ">
          <div className="text-center">
            <div className="text-2xl font-bold text-green-600">
              {backups.length > 0 
                ? new Date(backups[0].timestamp).toLocaleString('ko-KR')
                : ' '
              }
            </div>
            <div className="text-sm text-gray-600">
              {backups.length > 0 ? systemUtils.getBackupTypeDisplayName(backups[0].type) : '-'}
            </div>
          </div>
        </Card>
        <Card title=" ">
          <div className="text-center">
            <div className="text-2xl font-bold text-purple-600">
              {backups.length > 0 
                ? ((backups.filter(b => b.status === 'completed').length / backups.length) * 100).toFixed(1) + '%'
                : '0%'
              }
            </div>
            <div className="text-sm text-gray-600"> </div>
          </div>
        </Card>
      </div>

      {/*   */}
      <Card title=" ">
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-4 py-2 text-left"></th>
                <th className="px-4 py-2 text-left"></th>
                <th className="px-4 py-2 text-left"></th>
                <th className="px-4 py-2 text-left"></th>
                <th className="px-4 py-2 text-left"></th>
                <th className="px-4 py-2 text-left"></th>
                <th className="px-4 py-2 text-left"></th>
              </tr>
            </thead>
            <tbody>
              {backups.map(backup => (
                <tr key={backup.id} className="border-t hover:bg-gray-50">
                  <td className="px-4 py-2">
                    {new Date(backup.timestamp).toLocaleString('ko-KR')}
                  </td>
                  <td className="px-4 py-2">
                    <span className={`px-2 py-1 rounded text-xs ${
                      backup.type === 'full' ? 'bg-blue-100 text-blue-800' :
                      backup.type === 'incremental' ? 'bg-green-100 text-green-800' :
                      'bg-purple-100 text-purple-800'
                    }`}>
                      {systemUtils.getBackupTypeDisplayName(backup.type)}
                    </span>
                  </td>
                  <td className="px-4 py-2">
                    <span className={`px-2 py-1 rounded text-xs bg-${systemUtils.getStatusColor(backup.status)}-100 text-${systemUtils.getStatusColor(backup.status)}-800`}>
                      {backup.status}
                    </span>
                  </td>
                  <td className="px-4 py-2 font-mono">{backup.size}</td>
                  <td className="px-4 py-2">{backup.duration}</td>
                  <td className="px-4 py-2 font-mono text-xs">{backup.location}</td>
                  <td className="px-4 py-2">
                    <div className="flex space-x-1">
                      <button 
                        onClick={() => handleBackupDownload(backup.id)}
                        disabled={loading}
                        className="text-blue-600 hover:text-blue-800 text-xs disabled:opacity-50"
                      >
                        
                      </button>
                      <button 
                        onClick={() => handleBackupRestore(backup.id)}
                        disabled={loading}
                        className="text-green-600 hover:text-green-800 text-xs disabled:opacity-50"
                      >
                        
                      </button>
                      <button 
                        onClick={() => handleBackupDelete(backup.id)}
                        disabled={loading}
                        className="text-red-600 hover:text-red-800 text-xs disabled:opacity-50"
                      >
                        
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </Card>
    </div>
  );

  const renderMaintenanceSection = () => (
    <div className="space-y-6">
      <h3 className="text-lg font-semibold"> </h3>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <Card title=" ">
          <div className="space-y-4">
            {maintenanceTasks.filter(task => task.category === 'database').map(task => (
              <div key={task.id} className="flex justify-between items-center">
                <div>
                  <span className="font-medium">{task.name}</span>
                  {task.status === 'running' && (
                    <span className="ml-2 text-xs text-yellow-600"> ...</span>
                  )}
                </div>
                <Button 
                  variant="secondary" 
                  size="sm" 
                  onClick={() => handleMaintenanceTask(task.id)}
                  disabled={loading || task.status === 'running'}
                >
                  
                </Button>
              </div>
            ))}
            {maintenanceTasks.filter(task => task.category === 'database').length === 0 && (
              <div className="text-gray-500 text-sm text-center py-4">  .</div>
            )}
          </div>
        </Card>

        <Card title=" ">
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <span>  </span>
              <Button 
                variant="secondary" 
                size="sm" 
                onClick={() => handleCacheClear('application')}
                disabled={loading}
              >
                
              </Button>
            </div>
            <div className="flex justify-between items-center">
              <span>  </span>
              <Button 
                variant="secondary" 
                size="sm" 
                onClick={() => handleCacheClear('images')}
                disabled={loading}
              >
                
              </Button>
            </div>
            <div className="flex justify-between items-center">
              <span>  </span>
              <Button 
                variant="secondary" 
                size="sm" 
                onClick={() => handleCacheClear('sessions')}
                disabled={loading}
              >
                
              </Button>
            </div>
            <div className="flex justify-between items-center">
              <span>  </span>
              <Button 
                variant="primary" 
                size="sm" 
                onClick={() => handleCacheClear('all')}
                disabled={loading}
              >
                
              </Button>
            </div>
          </div>
        </Card>

        <Card title=" ">
          <div className="space-y-4">
            {maintenanceTasks.filter(task => task.category === 'logs').map(task => (
              <div key={task.id} className="flex justify-between items-center">
                <div>
                  <span className="font-medium">{task.name}</span>
                  {task.status === 'running' && (
                    <span className="ml-2 text-xs text-yellow-600"> ...</span>
                  )}
                </div>
                <Button 
                  variant="secondary" 
                  size="sm" 
                  onClick={() => handleMaintenanceTask(task.id)}
                  disabled={loading || task.status === 'running'}
                >
                  
                </Button>
              </div>
            ))}
            {maintenanceTasks.filter(task => task.category === 'logs').length === 0 && (
              <div className="text-gray-500 text-sm text-center py-4">  .</div>
            )}
          </div>
        </Card>

        <Card title="  ">
          <div className="space-y-4">
            {systemHealth ? (
              <>
                <div className="flex justify-between items-center">
                  <span> </span>
                  <span className={`text-sm text-${systemUtils.getStatusColor(
                    systemHealth.disk_usage > 90 ? 'error' : 
                    systemHealth.disk_usage > 75 ? 'warning' : 'good'
                  )}-600`}>
                    {systemHealth.disk_usage}%
                  </span>
                </div>
                <div className="flex justify-between items-center">
                  <span> </span>
                  <span className={`text-sm text-${systemUtils.getStatusColor(
                    systemHealth.memory_usage > 90 ? 'error' : 
                    systemHealth.memory_usage > 75 ? 'warning' : 'good'
                  )}-600`}>
                    {systemHealth.memory_usage}%
                  </span>
                </div>
                <div className="flex justify-between items-center">
                  <span> </span>
                  <span className={`text-sm text-${systemUtils.getStatusColor(systemHealth.network_status)}-600`}>
                    {systemHealth.network_status === 'good' ? '' : 
                     systemHealth.network_status === 'warning' ? '' : ''}
                  </span>
                </div>
                <div className="flex justify-between items-center">
                  <span></span>
                  <span className={`text-sm text-${systemUtils.getStatusColor(systemHealth.database_status)}-600`}>
                    {systemHealth.database_status === 'connected' ? '' : 
                     systemHealth.database_status === 'slow' ? '' : ' '}
                  </span>
                </div>
              </>
            ) : (
              <div className="space-y-2">
                <div className="flex justify-between items-center">
                  <span> </span>
                  <span className="text-gray-500 text-sm"> ...</span>
                </div>
              </div>
            )}
            <Button 
              variant="primary" 
              className="w-full mt-4"
              onClick={handleHealthCheck}
              disabled={loading}
            >
              {loading ? ' ...' : '   '}
            </Button>
          </div>
        </Card>
      </div>
    </div>
  );

  const renderUpdatesSection = () => (
    <div className="space-y-6">
      <h3 className="text-lg font-semibold"> </h3>

      <Card title="  ">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <div className="text-sm text-gray-600"> </div>
            <div className="text-lg font-medium">v2.1.3</div>
          </div>
          <div>
            <div className="text-sm text-gray-600"> </div>
            <div className="text-lg font-medium">v1.8.2</div>
          </div>
          <div>
            <div className="text-sm text-gray-600"> </div>
            <div className="text-lg font-medium">2024-03-01</div>
          </div>
          <div>
            <div className="text-sm text-gray-600"> </div>
            <div className="text-lg font-medium text-green-600"></div>
          </div>
        </div>
      </Card>

      <Card title="  ">
        <div className="space-y-4">
          <div className="p-4 border rounded">
            <div className="flex justify-between items-start">
              <div>
                <div className="font-medium">v2.2.0 -  </div>
                <div className="text-sm text-gray-600 mt-1">
                    AHP  <br/>
                       <br/>
                    
                </div>
                <div className="text-xs text-gray-500 mt-2">
                   : 2024-03-15
                </div>
              </div>
              <Button variant="primary" size="sm"></Button>
            </div>
          </div>

          <div className="p-4 border rounded bg-gray-50">
            <div className="text-center text-gray-600">
                .   .
            </div>
          </div>
        </div>
      </Card>

      <Card title=" ">
        <div className="space-y-3">
          {[
            { version: 'v2.1.3', date: '2024-03-01', type: '', description: '    ' },
            { version: 'v2.1.0', date: '2024-02-15', type: '', description: '   ' },
            { version: 'v2.0.0', date: '2024-01-30', type: '', description: '    AHP  ' }
          ].map((update, index) => (
            <div key={index} className="flex justify-between items-center p-3 border rounded">
              <div>
                <div className="font-medium">{update.version}</div>
                <div className="text-sm text-gray-600">{update.description}</div>
              </div>
              <div className="text-right">
                <div className="text-sm font-medium">{update.date}</div>
                <div className={`text-xs px-2 py-1 rounded ${
                  update.type === '' ? 'bg-red-100 text-red-800' :
                  update.type === '' ? 'bg-blue-100 text-blue-800' :
                  'bg-green-100 text-green-800'
                }`}>
                  {update.type}
                </div>
              </div>
            </div>
          ))}
        </div>
      </Card>
    </div>
  );

  if (loading) {
    return (
      <div className={`flex justify-center items-center h-64 ${className}`}>
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className={`space-y-6 ${className}`}>
      {/* Error Alert */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <div className="flex">
            <ExclamationTriangleIcon className="h-5 w-5 text-red-400" />
            <div className="ml-2">
              <h4 className="text-sm font-medium text-red-800"></h4>
              <p className="text-sm text-red-700 mt-1">{error}</p>
            </div>
            <button
              onClick={() => setError('')}
              className="ml-auto text-red-400 hover:text-red-600"
            >
              
            </button>
          </div>
        </div>
      )}

      {/* Success Alert */}
      {success && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-4">
          <div className="flex">
            <CheckCircleIcon className="h-5 w-5 text-green-400" />
            <div className="ml-2">
              <h4 className="text-sm font-medium text-green-800"></h4>
              <p className="text-sm text-green-700 mt-1">{success}</p>
            </div>
            <button
              onClick={() => setSuccess('')}
              className="ml-auto text-green-400 hover:text-green-600"
            >
              
            </button>
          </div>
        </div>
      )}
      {/*   */}
      <div className="border-b border-gray-200">
        <nav className="flex space-x-8">
          {[
            { id: 'config', name: ' ', icon: '' },
            { id: 'backup', name: ' ', icon: '' },
            { id: 'maintenance', name: '', icon: '' },
            { id: 'updates', name: '', icon: '' }
          ].map(section => (
            <button
              key={section.id}
              onClick={() => setActiveSection(section.id as 'config' | 'backup' | 'maintenance' | 'updates')}
              className={`py-2 px-1 border-b-2 font-medium text-sm flex items-center space-x-2 ${
                activeSection === section.id
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              <span>{section.icon}</span>
              <span>{section.name}</span>
            </button>
          ))}
        </nav>
      </div>

      {/*   */}
      {activeSection === 'config' && renderConfigurationSection()}
      {activeSection === 'backup' && renderBackupSection()}
      {activeSection === 'maintenance' && renderMaintenanceSection()}
      {activeSection === 'updates' && renderUpdatesSection()}

      {/* Confirmation Modal */}
      <Modal
        isOpen={showConfirmModal}
        onClose={() => setShowConfirmModal(false)}
        title=" "
      >
        <div className="space-y-4">
          <div className="flex items-center">
            <ExclamationTriangleIcon className="h-6 w-6 text-yellow-400 mr-3" />
            <p className="text-gray-700">{confirmAction?.message}</p>
          </div>
          
          <div className="flex justify-end space-x-3">
            <Button
              variant="outline"
              onClick={() => setShowConfirmModal(false)}
              disabled={loading}
            >
              
            </Button>
            <Button
              variant="primary"
              onClick={async () => {
                if (confirmAction) {
                  await confirmAction.action();
                  setShowConfirmModal(false);
                  setConfirmAction(null);
                }
              }}
              disabled={loading}
            >
              {loading ? ' ...' : ''}
            </Button>
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default SystemManagement;